import React, { useEffect } from "react";
import { useState } from "react";
import { RadioGroup } from "@headlessui/react";
import { CheckCircleIcon } from "@heroicons/react/solid";

function classNames(...classes) {
  return classes.filter(Boolean).join(" ");
}

const errorMessages = [
  "No es tan difcil",
  "Vamos, tu puedes",
  "Contina buscando",
];
function getRandomInt(min, max) {
  return Math.floor(Math.random() * (max - min)) + min;
}

export default function RadioButtons({
  question,
  onSubmit,
  error = false,
  setError = () => null,
}) {
  const { title, options } = question;

  const [selectedOption, setSelectedOption] = useState(options[0]);
  useEffect(() => {
    setError(false);
  }, [selectedOption]);
  const handleSubmit = (e) => {
    e.preventDefault();
    // console.dir(e);
    console.log("selected", selectedOption);
    onSubmit(selectedOption);
    // selectedOption.submit();
  };
  return (
    <form onSubmit={handleSubmit}>
      <RadioGroup value={selectedOption} onChange={setSelectedOption}>
        <RadioGroup.Label className="text-base text-lg">
          {title}
        </RadioGroup.Label>

        <div className="mt-4 grid grid-cols-1 gap-y-6 sm:grid-cols-3 sm:gap-x-4">
          {options.map((option) => (
            <RadioGroup.Option
              key={option.id}
              value={option}
              className={({ checked, active }) =>
                classNames(
                  checked ? "border-transparent" : "border-gray-300",
                  active ? "border-indigo-500 ring-2 ring-indigo-500" : "",
                  "relative bg-white border rounded-lg shadow-sm p-4 flex cursor-pointer focus:outline-none"
                )
              }
            >
              {({ checked, active }) => (
                <>
                  <span className="flex-1 flex">
                    <span className="flex flex-col">
                      <RadioGroup.Label
                        as="span"
                        className="block text-sm font-medium text-gray-900"
                      >
                        {option.title}
                      </RadioGroup.Label>
                      <RadioGroup.Description
                        as="span"
                        className="mt-1 flex items-center text-sm text-gray-500"
                      >
                        {option.description}
                      </RadioGroup.Description>
                      {/*<RadioGroup.Description*/}
                      {/*  as="span"*/}
                      {/*  className="mt-6 text-sm font-medium text-gray-900"*/}
                      {/*>*/}
                      {/*  {option.users}*/}
                      {/*</RadioGroup.Description>*/}
                    </span>
                  </span>
                  <CheckCircleIcon
                    className={classNames(
                      !checked ? "invisible" : "",
                      "h-5 w-5 text-indigo-600"
                    )}
                    aria-hidden="true"
                  />
                  <span
                    className={classNames(
                      active ? "border" : "border-2",
                      checked ? "border-indigo-500" : "border-transparent",
                      "absolute -inset-px rounded-lg pointer-events-none"
                    )}
                    aria-hidden="true"
                  />
                </>
              )}
            </RadioGroup.Option>
          ))}
        </div>
      </RadioGroup>
      <p
        className={`${!error && "invisible"} mt-1 text-sm text-red-600 py-2`}
        id="email-error"
      >
        {errorMessages[getRandomInt(0, errorMessages.length)]}
      </p>
      <button
        type="submit"
        className=" my-6 inline-flex items-center px-4 py-2 border border-transparent text-sm font-medium rounded-md shadow-sm text-white bg-indigo-600 hover:bg-indigo-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500"
      >
        Probar suerte
      </button>
    </form>
  );
}
